import React, { useLayoutEffect, useRef } from 'react'
import { ThemeCss } from './style'
import LevelCard from './LevelCard'
import w1 from '../../../assets/w1.png'
import w2 from '../../../assets/w2.png'
import w3 from '../../../assets/w3.png'
import { gsap } from 'gsap'

const Theme = () => {
    const data = [
        { pic: w1, title: ['The F2E', ''], text: '', company: '' },
        { pic: w2, title: ['', ''], text: 'Canvas', company: '' },
        { pic: w3, title: ['Scrum '], text: 'JS draggable', company: '' }
    ]
    const themeRef = useRef()
    useLayoutEffect(() => {
        gsap.set('.Week2', { opacity: 0, visibility: "hidden" });
        gsap.set('.Week3', { opacity: 0, visibility: "hidden" });
        let ctx = gsap.context(() => {
            gsap.timeline({
                scrollTrigger: {
                    trigger: ".themeTitle",
                    // markers: true,
                    start: 'top 50%',
                    end: 'top 5%',
                    scrub: true,
                }
            }).to('.Week2', {
                opacity: 1, visibility: "visible",
                duration: 1,
            }).to('.Week3', {
                opacity: 1, visibility: "visible",
                duration: 1,
            })
        }, themeRef)
        return () => ctx.revert()
    })

    return (
        <ThemeCss className="bg-black text-white" ref={themeRef}>
            <div className="container">
                <div className="row justify-content-center align-item-center">
                    <h2 className='text-center themeTitle'> </h2>
                    <div className='text dCenter'>
                        <p></p>
                    </div>
                    {data.map((ele, idx) => <LevelCard {...ele} key={idx} week={`Week${idx + 1}`} />)}
                </div>
            </div>
        </ThemeCss>
    )
}


export default Theme